/* src/App.module.css */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: rgb(245, 245, 245);
  /* padding-top: 50px; */
}

.sectionsContainer {
  display: flex;
  height: 100vh;
  position: relative;
}

.section {
  flex: 1;
  height: 94vh;
  background-size: cover;
  background-position: center;
  position: relative;
  transition: flex 1.3s, background-size 1.3s, background-position 1.3s;
  cursor: pointer;
}

.leftSection {
  background-image: url('./assets/backgroundsuit.png');
  /* filter: brightness(1.5); */
}

.rightSection {
  background-image: url('./assets/backgrounddeveloper.png');
}

.hovered {
  flex: 3;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: rgba(0, 0, 0, 0.6); */
  z-index: 1;
}

.topic {
  font-size: 2rem;
  margin-bottom: 20px;
  margin-top: 10vh;
  z-index: 2;
  color: rgb(149, 149, 149);
  white-space: nowrap; /* Ensure the text stays on one line */
  transition: font-size 1s ease; /* Slow down the font size transition */


}

.hovered .topic {
  font-size: 2.5rem; /* Increase font size on hover */
  color: rgb(255, 255, 255);
}

.middleImageContainer {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  width: auto;
  display: flex;
  align-items: center;
  z-index: 3; /* Ensures this container is above the background sections */
}

.personImage {
  height: 70%;
  margin-top: 17vh;
  transition: clip-path 1.3s, transform 1.3s, opacity 0.5s;
  opacity: 0.8; /* Adjust default opacity */
}

.leftImage {
  clip-path: inset(0 50% 0 0); /* Crops the right half */
  transform: translateX(50%);
}

.rightImage {
  clip-path: inset(0 0 0 50%); /* Crops the left half */
  transform: translateX(-50%);
}

.fullLeft {
  clip-path: inset(0 0 0 0); /* Shows full image on hover */
  transform: translateX(70%);
}

.fullRight {
  clip-path: inset(0 0 0 0); /* Shows full image on hover */
  transform: translateX(-70%);
}

.hidden {
  opacity: 0; /* Hide image */
  transform: scale(0.95); /* Slightly scale down when hidden */
}

.projectCardsContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 50px 0;
  margin-bottom: 15vh;
}

.typewritingText {
  position: absolute;
  text-align: left; /* Align text to the left */

  top: 20vh; /* Position text from the top */
  left: 70vh;
  color: #00ff00; /* Green text for coding effect */
  font-family: 'Courier New', Courier, monospace; /* Coding font */
  white-space: pre-wrap; /* Preserve whitespace and newlines */
  font-size: 0.8rem;
}


.businessContentContainer {
  display: flex;
  flex-wrap: wrap;
  width: 85vh;
  margin-top: -5vh;
  justify-content: center;
  align-items: flex-start;
  height: 120%;
  padding: 20px;
}

.businessCard {
  background: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 10px;
  height: 250px;
  margin: 10px; /* Adjust the margin to control spacing between cards */
  width: calc(33.33% - 20px); /* Three cards per row */
  box-sizing: border-box;
  opacity: 0;
  margin-bottom: 2vh;
  animation: fadeIn 1s ease-in-out forwards;
  font-family: 'Plus Jakarta Sans', sans-serif; /* Applies the Plus Jakarta font */

}

.businessCard:nth-child(4),
.businessCard:nth-child(5) {
  width: calc(50% - 20px); /* Ensure all cards are the same size */
  margin-top: -24vh; /* Space between the two rows */
}



.businessCard h3 {
  margin: 0;
}

.businessCard ul {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: left;
}

.businessCard ul li {
  margin: 5px 0;
}

@keyframes fadeIn {
  to {
    opacity: 0.9;
  }
}


.businessCard:nth-child(1) {
  animation-delay: 0.5s;
}

.businessCard:nth-child(2) {
  animation-delay: 1s;
}

.businessCard:nth-child(3) {
  animation-delay: 1.5s;
}

.businessCard:nth-child(4) {
  animation-delay: 2s;
}

.businessCard:nth-child(5) {
  animation-delay: 2.5s;
}


.seeMoreButton {
  background-color: #f05454;
  opacity: 0.7;
  color: rgb(255, 255, 255);
  width: 200px;
  height: 50px;
  font-size: 20px;
  z-index: 10; /* Ensure the button is above other elements */
  font-weight: bold;
  border-radius: 20px;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: -22vh; /* Adjust this as needed for spacing */
}

.seeMoreButton:hover {
  /* background-color: #c7c7c7; */
  opacity: 1;
  
}

.MyExpertise {
  color: rgb(0, 0, 0);
  font-size: 40px;
}

/* src/App.module.css */
.experienceCardsContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}


.SoloProjects {
  font-size: 3em;
}

.GroupProjects {
  font-size: 3em;

}

/* App.module.css */
.custom-cursor {
  position: fixed;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  background-color: blue;
  border-radius: 50%;
  pointer-events: none;
  transition: transform 0.1s ease-out;
  transform: translate(-50%, -50%);
  z-index: 10000;
}

.seeBusinessButton {
  background-color: #d72424;
  opacity: 0.7;
  color: rgb(255, 255, 255);
  width: 400px;
  height: 50px;
  font-size: 20px;
  font-weight: bold;
  border: none;
  border-radius: 20px;
  margin-bottom: 10vh;
  margin-top: 5vh;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.seeBusinessButton:hover {
  opacity: 1;
}



