/* src/components/ProjectCard.module.css */
.projectCard {
    width: 30%;
    background-color: rgb(255, 255, 255);
    height: 250px;
    padding: 20px;
    margin-top: -2vh;
    margin-bottom: 5vh;
    box-shadow: 
      0 2.8px 2.2px rgba(0, 0, 0, 0.034),
      0 6.7px 5.3px rgba(0, 0, 0, 0.048),
      0 12.5px 10px rgba(0, 0, 0, 0.06);
    border-radius: 20px;
    transition: transform 0.3s;
    cursor: pointer;
    margin-left: 20px;
    margin-right: 20px;
  }
  
  
  .projectCard:hover {
    transform: scale(1.05);
  }
  
  .projectCard h3 {
    margin: 0;
    margin-bottom: 10px;
  }
  
  .projectCard p {
    margin: 0;
  }
  
  .imageContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .icon {
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 10px;
    width: auto; /* Adjust the size as needed */
    height: 40px; /* Adjust the size as needed */
  }


  /* Define sizes for different icon sizes */
.small .icon {
    width: auto;
    height: 3.2vw;
    margin-left: 30px;
  }
  
  .medium .icon {
    width: auto;
    height: 2.6vw;
    margin-left: 20px;
    margin-top: 20px;
  }
  
  .large .icon {
    width: auto;
    height: 2.5vw;
    margin-left: 30px;
    margin-top: 10px;
  }

  .superlarge .icon {
    width: auto;
    height: 2.2vw;
    margin-left: 10px;
    margin-top: 20px;
  }
  .cardTitle {
    font-size: 30px;
    color: #858585;
  }

  .projectCard.inView {
    opacity: 1; /* Show when in view */
    transform: translateY(0); /* Move to original position */
    transition: opacity 0.6s ease-out, transform 0.6s ease-out; /* Smooth transition */
  }